import React from 'react';
import styled from 'styled-components';

import { Table } from 'react-bootstrap';

import ResultsRow from '../Atoms/ResultsRow';

const FigureColumn = styled.td`
    text-align: right;
`;

const Positive = styled(FigureColumn)`
    background-color: #aaffaa;
`;
const Borderline = styled(FigureColumn)`
    background-color: #fff999;
`;
const Negative = styled(FigureColumn)`
    background-color: #ffbbbb;
`;

class ResultsTable extends React.Component {
    generateElement(lower, upper) {
        return function (value) {
            const floatValue = parseFloat(value);

            switch (true) {
                case isNaN(floatValue):
                    return (<Negative>&mdash;</Negative>);
                case floatValue > upper:
                    return (<Negative>{value}</Negative>);
                case floatValue < lower:
                    return (<Positive>{value}</Positive>);
                default:
                    return (<Borderline>{value}</Borderline>);
            }
        };
    }

    render () {
        const elementGenerator = this.generateElement(
            parseFloat(this.props.lower),
            parseFloat(this.props.upper)
        );

        return (
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th rowSpan="2">Gene Symbol</th>
                        <th rowSpan="2">Ensembl ID</th>
                        <th rowSpan="2">Transcript Type</th>
                        <th rowSpan="2">Panels</th>
                        <th colSpan="4">Minimum Read Sequencing Depth</th>
                    </tr>
                    <tr>
                        <th>Blood</th>
                        <th>Fibroblasts</th>
                        <th>LCL</th>
                        <th>Muscle</th>
                    </tr>
                </thead>
                <tbody>
                { this.props.results.map(result => {
                    return (
                        <ResultsRow
                            result={result}
                            elementGenerator={elementGenerator}
                        />
                    );}) }
                </tbody>
            </Table>
        );
    }
}

export default ResultsTable;
