import React from 'react';
import styled from 'styled-components';

import { Table } from 'react-bootstrap';

const HeaderColumn = styled.td`
    text-align: right;
`;

const Positive = styled(HeaderColumn)`
    background-color: #aaffaa;
`;
const Borderline = styled(HeaderColumn)`
    background-color: #fff999;
`;
const Negative = styled(HeaderColumn)`
    background-color: #ffbbbb;
`;

class ResultsSummary extends React.Component {
    updateStats(lower, upper) {
        return function (stats, value) {
            const floatValue = parseFloat(value);

            switch (true) {
                case isNaN(floatValue):
                case floatValue > upper:
                    stats.negative++;
                    return stats;
                case floatValue < lower:
                    stats.positive++;
                    return stats;
                default:
                    stats.borderline++;
                    return stats;
            }
        }
    }

    generateStats(type) {
        return type.reduce(
            this.updateStats(
                parseFloat(this.props.lower),
                parseFloat(this.props.upper)
            ),
            {
                negative: 0,
                borderline: 0,
                positive: 0
            }
        );

    }

    render () {
        const stats = {
            blood: this.generateStats(this.props.results.map(result => result['MRSD(Blood)'])),
            fibroblasts: this.generateStats(this.props.results.map(result => result['MRSD(Fibroblasts)'])),
            lcl: this.generateStats(this.props.results.map(result => result['MRSD(LCL)'])),
            muscle: this.generateStats(this.props.results.map(result => result['MRSD(Muscle)'])),
            total: this.props.results.length
        };

        const generateTableCell = (value) => {
            const percentage = Math.round((parseFloat(value) / parseFloat(stats.total)) * 100, 1);

            return (
                <td>{ value } ({ percentage }%)</td>
            );
        };

        return (
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th rowSpan="2">Sequencing reads</th>
                        <th colspan="4">Genes covered (of { stats.total })</th>
                    </tr>
                    <tr>
                        <th>Blood</th>
                        <th>Fibroblasts</th>
                        <th>LCL</th>
                        <th>Muscle</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <Positive>&lt; {this.props.lower}M</Positive>
                        { generateTableCell(stats.blood.positive) }
                        { generateTableCell(stats.fibroblasts.positive) }
                        { generateTableCell(stats.lcl.positive) }
                        { generateTableCell(stats.muscle.positive) }
                    </tr>
                    <tr>
                        <Borderline>{this.props.lower}M to {this.props.upper}M</Borderline>
                        { generateTableCell(stats.blood.borderline) }
                        { generateTableCell(stats.fibroblasts.borderline) }
                        { generateTableCell(stats.lcl.borderline) }
                        { generateTableCell(stats.muscle.borderline) }
                    </tr>
                    <tr>
                        <Negative>&gt; {this.props.upper}M</Negative>
                        { generateTableCell(stats.blood.negative) }
                        { generateTableCell(stats.fibroblasts.negative) }
                        { generateTableCell(stats.lcl.negative) }
                        { generateTableCell(stats.muscle.negative) }
                    </tr>
                </tbody>
            </Table>
        );
    }
}

export default ResultsSummary;
