import React from 'react';

class ResultsRow extends React.Component {
    render() {
        const result = this.props.result;

        const blood = this.props.elementGenerator(result['MRSD(Blood)']);
        const fibro = this.props.elementGenerator(result['MRSD(Fibroblasts)']);
        const lcl = this.props.elementGenerator(result['MRSD(LCL)']);
        const muscle = this.props.elementGenerator(result['MRSD(Muscle)']);

        return (
            <tr key={result.gene_symbol}>
                <td>{result.gene_symbol}</td>
                <td>{result.ensembl_id}</td>
                <td>{result.transcript_type}</td>
                <td>{result.panels}</td>
                { blood }
                { fibro }
                { lcl }
                { muscle }
            </tr>
        );
    }
}

export default ResultsRow;
