import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import './App.scss';
import { Container, Row, Col, Jumbotron } from "react-bootstrap";

import { readRemoteFile } from "react-papaparse";
import NewSearch from './Organisms/NewSearch';
import Results from './Organisms/Results';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cache: {},
            data: null,
            panelList: [],
            selectedPanel: null,
            spliceJunctionCoverage: 8,
            coverageThreshold: 75,
            confidenceLevel: 95
        };
    }

    componentDidMount() {
        let component = this;

        readRemoteFile('./data/PanelApp_panel_names.tsv', {
            header: false,
            delimiter: "\t",
            complete: (parsed => {
                component.setState({
                    panelList: parsed.data.map((item) => item[0].trim()).filter((item) => item !== '')
                });
            })
        });

        this.loadData();
    }

    generateFileName(spliceJunctionCoverage, coverageThreshold, confidenceLevel) {
        return `./data/all_GENCODE_${spliceJunctionCoverage}_0.${coverageThreshold}_0.${confidenceLevel}.results.master.short.tsv`;
    }

    loadData() {
        let component = this;
        const filename = this.generateFileName(
            this.state.spliceJunctionCoverage,
            this.state.coverageThreshold,
            this.state.confidenceLevel
        );

        if (this.state.cache[filename] !== undefined) {
            this.setState({
                data: this.state.cache[filename]
            });
            return;
        }

        readRemoteFile(filename, {
            header: true,
            delimiter: "\t",
            complete: (parsed => {
                let cache = component.state.cache;
                cache[filename] = parsed.data;
                component.setState({
                    cache: cache,
                    data: cache[filename]
                })
            })
        });
    }

    updateDataFile(component) {
        return async (data) => {
            await component.setState(data);
            component.loadData();
        };
    }

    setSearchTerms(component) {
        return (data) => {
            component.setState(data);
        };
    }

    clearSearchTerms(component) {
        return () => {
            component.setState({
                geneList: null,
                selectedPanel: null
            });
        };
    }

    getSearchResults(component) {
        return function () {
            if (!component.state || !component.state.data || !component.state.geneList) {
                return [];
            }

            return component.state.data
                .filter(item => {
                    const panelList = item.panels ? item.panels.split(';') : [];

                    return (component.state.geneList.indexOf(item.gene_symbol) !== -1)
                        || (panelList.indexOf(component.state.selectedPanel) !== -1);
                });
        };
    }

    render() {
        return (
            <Router>
                <Container>
                    <Row>
                        <Col>
                            <Jumbotron>
                                <h1>MRSD Web Portal</h1>
                            </Jumbotron>
                        </Col>
                    </Row>

                    <Row>
                        <Col>
                            <Switch>
                                <Route
                                    path="/results"
                                    render={() => {
                                        return (
                                            !this.state || !this.state.geneList || !this.state.data ?
                                                <Redirect to="/" /> :
                                                <Results
                                                    clearSearchTerms={this.clearSearchTerms(this)}
                                                    getSearchResults={this.getSearchResults(this)}
                                                />
                                        );
                                    }}
                                />
                                <Route
                                    path="/"
                                    render={() => {
                                        return (
                                            this.state && this.state.geneList && this.state.data ?
                                                <Redirect to="/results" /> :
                                                <NewSearch
                                                    setSearchTerms={this.setSearchTerms(this)}
                                                    updateDataFile={this.updateDataFile(this)}
                                                    panelList={this.state.panelList}
                                                    spliceJunctionCoverage={this.state.spliceJunctionCoverage}
                                                    coverageThreshold={this.state.coverageThreshold}
                                                    confidenceLevel={this.state.confidenceLevel}
                                                />
                                        );
                                    }}
                                />
                            </Switch>
                        </Col>
                    </Row>
                </Container>
            </Router>
        );
    }
}

export default App;
