import React from 'react';
import styled from 'styled-components';

import { Container, Row, Col } from 'react-bootstrap';

const ControlWrapper = styled(Container)`
    margin-bottom: 2em;
`;

class HighlightControls extends React.Component {
    constructor(props) {
        super(props);
        this.state = { lowerThreshold: 50, upperThreshold: 100 };

        this.handleChange = this.handleChange.bind(this);
        this.onUpdatedValues = props.handleChange;
    }

    handleChange(event) {
        const target = event.target;
        const name = target.name;
        const value = parseInt(target.value, 10);

        this.setState({
            [name]: value
        });

        let returnedValue = this.state;
        returnedValue[name] = value;

        this.onUpdatedValues(returnedValue);
    }

    render() {
        return (
            <ControlWrapper>
                <Row>
                    <Col>
                        <label className="form-label" htmlFor="lowerThreshold">Lower bound</label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                name="lowerThreshold"
                                type="number"
                                min="0"
                                onChange={this.handleChange}
                                value={this.state.lowerThreshold}
                            />
                            <div className="input-group-append">
                                <span className="input-group-text">million</span>
                            </div>
                        </div>
                    </Col>
                    <Col>
                        <label className="form-label" htmlFor="upperThreshold">Upper bound</label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                name="upperThreshold"
                                type="number"
                                min="0"
                                onChange={this.handleChange}
                                value={this.state.upperThreshold}
                            />
                            <div className="input-group-append">
                                <span className="input-group-text">million</span>
                            </div>
                        </div>
                    </Col>
                </Row>
            </ControlWrapper>
        );
    }
}

export default HighlightControls;
