import React from 'react';

import { Formik, Form } from 'formik';
import { Alert, Button, Tab, Tabs } from 'react-bootstrap';
import DataControls from '../Molecules/DataControls';
import GeneList from '../Molecules/GeneList';
import PanelList from '../Molecules/PanelList';
import Introduction from '../Molecules/Introduction';

class NewSearch extends React.Component {
    parseGeneList(data) {
        return data
            .split("\n")
            .map(val => val.trim().toUpperCase())
            .filter(val => val !== '');
    }

    render() {
        const setSearchTerms = this.props.setSearchTerms;
        const updateDataFile = this.props.updateDataFile;
        const component = this;

        return (
            <>
                <h2>New search</h2>
                <Formik
                    initialValues={{ geneList: '' }}
                    validate={ values => {
                        const errors = {};

                        if ((component.parseGeneList(values.geneList).length === 0) && (values.selectedPanel === '' || values.selectedPanel === undefined)) {
                            errors.geneList = 'You must supply either a PanelApp panel, or a list of genes to generate the data set';
                        }

                        return errors;
                    }}
                    onSubmit={(values, { setSubmitting }) => {
                        setSearchTerms({
                            geneList: values.selectedPanel ? [] : component.parseGeneList(values.geneList),
                            selectedPanel: values.selectedPanel
                        });
                    }}
                >
                    {({
                        values,
                        errors,
                        touched,
                        handleChange,
                        handleBlur,
                        handleSubmit,
                        isSubmitting
                    }) => (
                        <Form>
                            <Tabs defaultActiveKey="geneListTab">
                                <Tab eventKey="geneListTab" title="Gene list" data-testid="geneListTab">
                                    <DataControls
                                        handleChange={updateDataFile}
                                        spliceJunctionCoverage={component.props.spliceJunctionCoverage}
                                        coverageThreshold={component.props.coverageThreshold}
                                        confidenceLevel={component.props.confidenceLevel}
                                    />
                                    <PanelList
                                        panels={component.props.panelList}
                                        handleChange={handleChange}
                                    />
                                    <GeneList
                                        handleChange={handleChange}
                                        handleBlur={handleBlur}
                                        value={values.geneList}
                                    />
                                </Tab>
                                <Tab eventKey="aboutTab" title="About">
                                    <Introduction />
                                </Tab>
                            </Tabs>
                            { errors.geneList && touched.geneList &&
                                <Alert variant="danger" data-testid="error-message">
                                    { errors.geneList }
                                </Alert>
                            }
                            <Button variant="primary" type="submit">
                                Search
                            </Button>
                        </Form>
                    )}
                </Formik>
            </>
        );
    }
}

export default NewSearch;
