import React from 'react';
import styled from 'styled-components';

import { Container, Row, Col } from 'react-bootstrap';

const ControlWrapper = styled(Container)`
    margin-bottom: 2em;
`;

class DataControls extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.onUpdatedValues = props.handleChange;
    }

    handleChange(event) {
        const target = event.target;
        const name = target.name;
        const value = parseInt(target.value, 10);
        let controls = {
            spliceJunctionCoverage: this.props.spliceJunctionCoverage,
            coverageThreshold: this.props.coverageThreshold,
            confidenceLevel: this.props.confidenceLevel
        };
        controls[name] = value;

        this.onUpdatedValues(controls);
    }

    render() {
        return (
            <ControlWrapper>
                <Row>
                    <Col>
                        <label className="form-label" htmlFor="spliceJunctionCoverage">Splice junction read coverage</label>
                        <div className="input-group">
                            <input
                                className="form-control"
                                name="spliceJunctionCoverage"
                                type="number"
                                min="1"
                                max="20"
                                onChange={this.handleChange}
                                value={this.props.spliceJunctionCoverage}
                            />
                        </div>
                    </Col>
                    <Col>
                        <label className="form-label" htmlFor="coverageThreshold">Proportion of splice junctions covered</label>
                        <div className="input-group">
                            <select
                                className="form-control"
                                name="coverageThreshold"
                                onChange={this.handleChange}
                                value={this.props.coverageThreshold}
                            >
                                <option>75</option>
                                <option>95</option>
                            </select>
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </Col>
                    <Col>
                        <label className="form-label" htmlFor="confidenceLevel">Confidence level</label>
                        <div className="input-group">
                            <select
                                className="form-control"
                                name="confidenceLevel"
                                onChange={this.handleChange}
                                value={this.props.confidenceLevel}
                            >
                                <option>95</option>
                                <option>99</option>
                            </select>
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </Col>
                </Row>
            </ControlWrapper>
        );
    }
}

export default DataControls;
