import React from 'react';
import styled from 'styled-components';

const DefaultValue = styled.em`
    font-style: normal;
    text-decoration: underline;
`;

function Introduction() {
    return (
        <>
            <p>
                This portal allows the generation of <strong>minimum required
                sequencing depth</strong>, or <strong>MRSD</strong>, values for
                genes and panels of interest.  The MRSD is the number (in <em>millions</em>)
                of sequencing reads required from RNAseq experiments to
                confidently determine aberrant splicing events for a gene of
                interest.
            </p>

            <p>
                The MRSD is uniquely calculated for each gene and tissue of
                interest, and can guide the suitability of RNA extracted from
                different tissues to determine significant changes in splicing
                profiles.
            </p>

            <p>
                The user can customise the <strong>confidence level</strong> (the
                proportion of individuals in the GTEx dataset that would meet
                specified criteria, <DefaultValue>default=95%</DefaultValue>),
                the <strong>read coverage</strong> (number of uniquely mapped reads
                spanning canonical splice junctions, <DefaultValue>default=8</DefaultValue>),
                and the <strong>splice junction proportion</strong> (the desired
                proportion of splice junctions with adequate coverage in the gene of
                interest, <DefaultValue>default=75%</DefaultValue>).
            </p>

            <p>
                More details on the methodology are available (here).  If you require any
                further help or support please contact <a href="mailto:jamie.ellingford@manchester.ac.uk">Jamie Ellingford</a>.
            </p>
        </>
    );
}

export default Introduction;
