import React from 'react';

import { Form } from 'react-bootstrap';

class GeneList extends React.Component {
    render() {
        return (
            <Form.Group controlId="selectedPanel">
                <Form.Label>PanelApp panel</Form.Label>
                <Form.Control
                    as="select"
                    rows="10"
                    data-testid="panelListInput"
                    onChange={this.props.handleChange}
                >
                    <option></option>
                    { this.props.panels.map(panel => {
                    return (
                        <option key="{panel}">{panel}</option>
                    );}) }
                </Form.Control>
            </Form.Group>
        );
    }
}

export default GeneList;
