import React from 'react';
import { jsonToCSV } from 'react-papaparse';

import { Button } from 'react-bootstrap';

import ResultsTable from '../Molecules/ResultsTable';
import HighlightControls from '../Molecules/HighlightControls';
import ResultsSummary from '../Molecules/ResultsSummary';

class Results extends React.Component {
    componentDidMount() {
        this.setState({ results: this.props.getSearchResults() });
    }

    generateTsv(data) {
        return 'data:text/tab-separated-values;charset=utf8,' + 
            jsonToCSV(data, { delimiter: "\t", header: true });
    }

    updateHighlights(component) {
        return function (values) {
            component.setState(values);
        };
    }

    render() {
        const component = this;
        const results = this.props.getSearchResults();
        const downloadString = this.generateTsv(results);

        const lowerThreshold = (this.state && this.state.lowerThreshold) ?? 50;
        const upperThreshold = (this.state && this.state.upperThreshold) ?? 100;

        return (
            <div>
                <HighlightControls
                    handleChange={component.updateHighlights(component)}
                />
                <Button variant="primary" onClick={this.props.clearSearchTerms}>New search</Button>
                &nbsp;
                <Button href={downloadString} variant="secondary" download="msrd_results.tsv">Download as TSV</Button>
                <ResultsSummary
                    results={results}
                    lower={lowerThreshold}
                    upper={upperThreshold}
                />
                <ResultsTable
                    results={results}
                    lower={lowerThreshold}
                    upper={upperThreshold}
                />
                <Button variant="primary" onClick={this.props.clearSearchTerms}>New search</Button>
                &nbsp;
                <Button href={downloadString} variant="secondary" download="msrd_results.tsv">Download as TSV</Button>
            </div>
        );
    }
}

export default Results;
